// WaffleChart.react.js
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import * as d3 from 'd3';
import { gsap } from 'gsap';

const uncount = (data, accessor) =>
  data.reduce((arr, item) => {
    const count = accessor(item);
    for (let i = 0; i < count; i++) {
      arr.push({
        ...item,
      });
    }
    return arr;
  }, []);

export default class WaffleChart extends Component {
  componentDidMount() {
    this.initializeWaffleChart();
  }

  initializeWaffleChart() {
    const { width, data, valueName, barHeightName, groupName, colors } = this.props;

    const scaleColor = d3
      .scaleOrdinal()
      .domain(data.map((d) => d.year))
      .range(colors);


    const boxSize = 20;
    // convert raw data values to boxes based on conversion factor
    data.forEach((d) => d.boxes = Math.ceil(d[valueName] / 1000))

    const boxes = uncount(data, (d) => d.boxes);

    const nestedData = Array.from(
        d3.group(boxes, (d) => d[groupName]),
        ([key, values]) => ({ key, values })
    );

    const graph = d3.select(`#${this.props.id}`)


    const group = graph
      .selectAll('.container')
      .data(nestedData)
      .join('div')
      .attr('class', 'container')

    if (barHeightName) {
      // determine the max bar data value
      const maxBarData = Math.max(...data.map((d) => d[barHeightName]));
      // determine the min bar data value
      const minBarData = Math.min(...data.map((d) => d[barHeightName]));
      // determine rescale factor
      const rescaleFactor = maxBarData - minBarData;
      
      group
        .append('div')
        .data(data)
        .attr('class', 'data-bar')
        .style('width', '100%')
        .style('height', (d) => {
          console.log(d[barHeightName]);
          return `${(d[barHeightName] - minBarData) / rescaleFactor * 500}px`;
        })
        .style('background-color', '#9ca589')
    }

    const labelDiv = group
      .append('div')
      .attr('class', 'label')
      .style('width', '100%')

    labelDiv
      .append('div')
      .text((d) => d.key)
      .style('width', '100%')
    
    const target = this.props.icon ? 'box-icon' : 'box';

    if (this.props.icon) {
      group
        .selectAll(`.${target}`)
        .data((d) => d.values)
        .join('div')
        .attr('class', target)
        .style('width', `${boxSize}px`)
        .style('height', `${boxSize}px`)
        .style('background-image', (d) => `url(${d.iconUrl})`);
    }
    else {
      group
        .selectAll(`.${target}`)
        .data((d) => d.values)
        .join('div')
        .attr('class', target)
        .style('background-color', (d) => scaleColor(d.year));
    }
    

    // Initiate paused animation
    let tl = gsap.timeline({ paused: true });

    tl.to(`.${target}`, {
        scale: 1,
        ease: "back.out(1.7)", // You can adjust the easing as needed
        stagger: {
            grid: "auto",
            from: "start",
            axis: "y",
            amount: 0.08, // Use "amount" instead of "each"
        },
    });

    // Initiate paused animation for bars
    let tl2 = gsap.timeline({ paused: true });

    tl2.from('.data-bar', {
      height: 0, 
      ease: 'power1.inOut', // You can adjust the easing as needed
      stagger: {
          grid: 'auto',
          from: 'start',
          axis: 'y',
          amount: 0.08,
      },
    })

    this.anim = tl;
    this.barAnim = tl2;
  }

  playAnimation = () => {
    if (!this.anim.isActive()) {
      this.anim.play(0);
    }
    if (!this.barAnim.isActive()) {
      this.barAnim.play(0);
    }
  };

  reverseAnimation = () => {
    this.anim.reverse();
    this.barAnim.reverse();

  };

  render() {
    const { width } = this.props;
    return (
      <div>
        <div id={this.props.id} className='chart' style={{'width': `${width}px`}} />
        <div id='button-container' style={{
          'width': `${width}px`
        }}>
          <button onClick={this.playAnimation} style={{
            'marginLeft': '20px',
            'marginRight': '20px'
          }}>Play Animation</button>
          <button onClick={this.reverseAnimation} style={{
            'marginRight': '20px',
            'marginLeft': '20px'
          }}>Reverse Animation</button>
        </div>
      </div>
    );
  }
}

WaffleChart.defaultProps = {
  colors: ['#FF8E79', '#FF6B5B', '#FF4941', '#DB1D25'],
  width: 600,
  icon: false
};

WaffleChart.propTypes = {
  id: PropTypes.string.isRequired,
  width: PropTypes.number,
  data: PropTypes.arrayOf(PropTypes.any).isRequired,
  valueName: PropTypes.string.isRequired,
  barHeightName: PropTypes.string,
  groupName: PropTypes.string.isRequired,
  icon: PropTypes.bool,
  colors: PropTypes.arrayOf(PropTypes.string),
};
